.about {
  background: var(--primary-bg-pink);
  padding-top: 125px;
  @media screen and (min-width: 768px) {
    padding-top: 130px;
  }
  @media screen and (min-width: 1280px) {
    padding-top: 125px;
  }
}
.about__container {
  background-image: url('../images/mobile/background-1-320@1x.png');
  background-repeat: no-repeat;
  background-size: contain;
  background-position: center 86%;

  @media (min-device-pixel-ratio: 2),
    (-webkit-min-device-pixel-ratio: 2),
    (min-resolution: 192dpi),
    (min-resolution: 2dppx) {
    background-image: url('../images/mobile/background-1-320@2x.png') no-repeat center 34%;
  }
  @media screen and (min-width: 768px) {
    background-image: none;
  }
  @media screen and (min-width: 1280px) {
    background-image: url('../images/desktop/sectionbg1200@1x.png');
    background-repeat: no-repeat;
    background-size: contain;
    background-position: center 89%;

    @media (min-device-pixel-ratio: 2),
      (-webkit-min-device-pixel-ratio: 2),
      (min-resolution: 192dpi),
      (min-resolution: 2dppx) {
      background-image: url('../images/desktop/sectionbg1200@2x.png') no-repeat center 34%;
    }
  }
}

.about__lable {
  display: block;
  font-family: Titan One;
  font-size: 18px;
  line-height: 1.17;
  text-align: center;
  text-transform: uppercase;

  margin-bottom: 15px;
  color: var(--secondary-color);

  @media screen and (min-width: 768px) {
    font-size: 26px;
    line-height: 1.15;
  }
  @media screen and (min-width: 1280px) {
    font-size: 30px;
    line-height: 1.13;
  }
}
.about__title {
  font-family: Titan One;
  font-style: normal;
  font-weight: normal;
  font-size: 34px;
  line-height: 1.15;
  text-align: center;
  letter-spacing: 0.04em;
  text-transform: uppercase;
  margin-bottom: 26px;
  color: var(--primary-text);
  @media screen and (min-width: 768px) {
    font-size: 48px;
    line-height: 1.14;
  }
  @media screen and (min-width: 1280px) {
    font-size: 58px;
    line-height: 1.13;
  }
}
.about__content {
  @media screen and (min-width: 768px) {
    display: flex;
    align-items: flex-start;
  }
