.about__picture {
  margin-bottom: 46px;

  @media screen and (min-width: 768px) {
    // width: 340px;
    // height: 340px;
    margin-bottom: 0;
  }
  @media screen and (min-width: 1280px) {
    width: 550px;
    height: 550px;
    // padding-left: 114px;
    // padding-right: 55px;
  }
}
.about__block {
  padding-left: 30px;
  padding-right: 30px;
  @media screen and (min-width: 768px) {
    width: 340px;
    padding-left: 14px;
    padding-right: 0;
  }

  @media screen and (min-width: 1280px) {
    width: 463px;
    padding-left: 74px;
  }
}

.about__text {
  display: block;
  font-family: DM Sans;
  font-weight: 500;
  font-size: 12px;
  line-height: 1.62;
  text-align: left;
  margin-bottom: 30px;

  color: var(--primary-black);

  @media screen and (min-width: 768px) {
    font-size: 14px;
    line-height: 1.76;
    padding-top: 35px;
  }
  @media screen and (min-width: 1280px) {
    font-size: 16px;
    letter-spacing: 0.04em;
  }
}
.about__text--second {
  display: block;
  font-family: DM Sans;
  font-weight: 500;
  font-size: 12px;
  line-height: 1.62;
  text-align: left;
  margin-bottom: 17px;
  color: var(--secondary-text);

  @media screen and (min-width: 768px) {
    font-size: 14px;
    line-height: 1.76;
    letter-spacing: 0.04em;
    margin-bottom: 62px;
  }
  @media screen and (min-width: 1280px) {
    font-size: 14px;
    // padding-right: 117px;
    margin-bottom: 45px;
  }
}
.about__button {
  display: inline-flex;
  width: 176px;
  height: 44px;
  background: var(--primary-white);
  border-radius: 22px;
  border: none;

  font-family: DM Sans;
  font-weight: bold;
  font-size: 14px;
  line-height: 18px;

  justify-content: center;
  align-items: center;
  color: var(--primary-text);
  cursor: pointer;
  @include animation;

  &:hover,
  &:focus {
    background-color: var(--hover-color);
  }
}
.about__button__icon {
  margin-left: 12px;
}
